import TradingViewWidget, { Themes } from 'react-tradingview-widget';
import { Row, Col } from 'react-bootstrap';
import {useEffect, useState} from 'react';

import Page from "../../components/Page";
import { ResponsiveCard } from "../../components/Card";
import styled from "styled-components";
import marketTokens from "../../constants/marketTokens.json";

const StyledResponsiveCard = styled(ResponsiveCard)`
  margin-top: -30px;
  overflow: hidden;
  
  .card-body {
    padding: 0;
    min-height: 600px;
    
    & article div {
      width: 100% !important;
    }
  } 
  
`

const tokens = marketTokens.tokens.filter(token => token.tags.includes('erc20')).map(token => token.symbol.toLowerCase());
const regex = t => new RegExp('^' + t + '|' + t + '$', 'g')

const Spot = props => {
    const [pair, setPair] = useState('BINANCE:BNBBUSD');

    useEffect(() => {
        if(props.match.params.pair === undefined) {
            setPair('BINANCE:BNBBUSD');
        } else {
            const p = props.match.params.pair.toLowerCase();

            const matchedPairs = tokens.filter(t => {
                return regex(t).test(p);
            });

            if(matchedPairs.length > 0) {
                setPair(`BINANCE:${p.toUpperCase()}`)
            } else {
                props.history.push('/markets')
            }
        }
    }, [props.match.params])

    return (
        <Page title={'Spot'} morePadding>
            <Row>
                <Col>
                    <StyledResponsiveCard>
                        <TradingViewWidget
                            symbol={pair}
                            theme={Themes.DARK}
                            allow_symbol_change={false}
                            calendar={true}
                            details={true}
                            hotlist={true}
                            />
                    </StyledResponsiveCard>
                </Col>
            </Row>
        </Page>
    )
}

export default Spot;
